<template>
  <div class="home">
    <header class="header">
      <VSearch />
      <VMenu />
    </header>
    <router-view
      :list="newsList"
      class="view"/>
    <VFooter />
  </div>
</template>
<script>
import VSearch from "@/components/Search";
import VMenu from "@/components/Menu";
import VFooter from "@/components/Footer";
import axios from "axios"
export default {
  name: "home",
  components: {
    VSearch,
    VMenu,
    VFooter
  },
  data () {
    return {
      bannerList: [],
      newsList: []
    }
  },
  created () {
    this.getInfo()
  },
  methods: {
    getInfo () {
      this.$http([this.getBannerInfo(), this.getList()])
      .then(result => {
        this.bannerList = result[0]
        this.newsList = result[1]
      })
    },
    getBannerInfo () {
      const config = this.$path.get_banner
      return this.$http(config).then((data) => {
        return data;
      })
    },
    getList () {
      const config = this.$path.list;
      config.params = { cate: this.$route.meta.cate };
      return this.$http(config).then((result) => {
        const {code, data} = result;
        if (code === "0") {
          return data;
        }
        return result;
      })
    }
  }
};
</script>

<style scoped lang="stylus">
.header
  position fixed
  top 0
  z-index 10
  width 100%
  max-width 750px;
  height 10rem
  background #ffffff
.view
  position absolute
  top 10rem
  bottom 4rem
  background #ffffff
</style>
